'use client';

import React from 'react';
import { useTransactions } from '@/context/TransactionContext';
import { formatCurrency } from '@/lib/transaction';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

export const Dashboard: React.FC = () => {
  const { getTotalCredits, getTotalDebits, getBalance, transactions } = useTransactions();

  const totalCredits = getTotalCredits();
  const totalDebits = getTotalDebits();
  const balance = getBalance();

  return (
    <div className="w-full max-w-6xl mx-auto p-6">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">Financial Overview</h1>
        <p className="text-muted-foreground">Track your income and expenses</p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <Card className="border-2">
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground uppercase tracking-wide">
              Total Credits
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">
              {formatCurrency(totalCredits)}
            </div>
            <p className="text-xs text-muted-foreground mt-1">
              Income received
            </p>
          </CardContent>
        </Card>

        <Card className="border-2">
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground uppercase tracking-wide">
              Total Debits
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">
              {formatCurrency(totalDebits)}
            </div>
            <p className="text-xs text-muted-foreground mt-1">
              Expenses paid
            </p>
          </CardContent>
        </Card>

        <Card className="border-2">
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground uppercase tracking-wide">
              Net Balance
            </CardTitle>
          </CardHeader>
          <CardContent>
            <div className={`text-2xl font-bold ${balance >= 0 ? 'text-green-600' : 'text-red-600'}`}>
              {formatCurrency(balance)}
            </div>
            <p className="text-xs text-muted-foreground mt-1">
              Current balance
            </p>
          </CardContent>
        </Card>
      </div>

      {transactions.length === 0 && (
        <Card className="border-2 border-dashed">
          <CardContent className="flex flex-col items-center justify-center py-12">
            <div className="text-center">
              <h3 className="text-lg font-semibold text-muted-foreground mb-2">
                No transactions yet
              </h3>
              <p className="text-sm text-muted-foreground">
                Add your first transaction to get started with tracking your finances
              </p>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
};
